*,
*::before,
*::after {
  box-sizing: border-box;
}

.App {
  --header_height: 60px;

  padding: 0px;
  margin: 0px;
  border: none;

  margin: 0 auto;
  max-width: 1280px;

}

.hw {
  padding: 24px 0;

  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;

  
}

.hwTitle {
  font-family: "Montserrat", sans-serif;
  font-size: 22px;
  line-height: 28px;

  font-weight: 600;

  padding-top: 120px;

  
}
